:root {
  --background: #030303;
  --foreground: #ededed;
  --muted: #0f0f10;
  --muted-2: #0c0c0d;
  --accent: #ffffff;
  --text-dim: #a1a1aa;
  --line-start: #1A2226;
  --line-mid: #1E3A47;
  --line-end: #203E35;
}
.lzr-background {
  position: relative;
  background: url('/heroimage.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.lzr-background::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(
      to right,
      rgba(224,236,236,0.06) 0px,
      rgba(224,236,236,0.06) 1px,
      transparent 1px,
      transparent 24px
    ),
    repeating-linear-gradient(
      to bottom,
      rgba(224,236,236,0.06) 0px,
      rgba(224,236,236,0.06) 1px,
      transparent 1px,
      transparent 24px
    );
  opacity: .15;
  mask-image: radial-gradient(1200px 600px at 50% 20%, black 40%, transparent 80%);
  pointer-events: none;
}
.lzr-background::after {
  content: "";
  position: absolute;
  inset: 0;
  background: repeating-linear-gradient(
    90deg,
    transparent 0 50px,
    rgba(30,58,71,0.10) 50px 52px
  );
  mix-blend-mode: screen;
  opacity: .08;
  animation: shimmer 12s linear infinite;
  pointer-events: none;
}
@keyframes shimmer {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(50px);
  }
}
.nav-compact {
  text-transform: uppercase;
  letter-spacing: .16em;
  font-size: 10px;
}
.kicker {
  text-transform: uppercase;
  letter-spacing: .16em;
  color: var(--text-dim);
}
.headline-xl {
  font-weight: 800;
  line-height: .9;
}
.btn-outline {
  border: 1px solid rgba(255,255,255,.2);
  background: transparent;
}
.btn-outline:hover {
  border-color: rgba(255,255,255,.35);
}
.section {
  padding-top: 6rem;
  padding-bottom: 6rem;
}
body {
  background: linear-gradient(135deg,
      rgba(26,34,38,0.85) 0%,
      rgba(30,58,71,0.55) 40%,
      rgba(32,62,53,0.55) 70%)
    , var(--background);
  background-attachment: fixed;
  color: var(--foreground);
}
.diagram-grid {
  position: relative;
  background: linear-gradient(to bottom, transparent, transparent),
              linear-gradient(to right, transparent, transparent);
}
.diagram-grid::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(
      0deg,
      transparent 0,
      transparent 23px,
      rgba(255,255,255,.06) 23px,
      rgba(255,255,255,.06) 24px
    ),
    repeating-linear-gradient(
      90deg,
      transparent 0,
      transparent 23px,
      rgba(255,255,255,.06) 23px,
      rgba(255,255,255,.06) 24px
    );
  opacity: .12;
  pointer-events: none;
}
.anim-scan {
  position: relative;
  overflow: hidden;
}
.anim-scan::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: -100%;
  right: -100%;
  background: repeating-linear-gradient(90deg, rgba(255,255,255,.0) 0 18px, rgba(255,255,255,.25) 18px 20px);
  animation: scan 6s linear infinite;
  opacity: .2;
}
@keyframes scan {
  0% {
    transform: translateX(-20px);
  }
  100% {
    transform: translateX(40px);
  }
}
.executor-burst {
  width: 120px;
  height: 120px;
  position: relative;
}
.executor-burst span {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 2px;
  height: 26px;
  background: rgba(255,255,255,.7);
  transform-origin: center -6px;
  border-radius: 1px;
}
.executor-burst {
  animation: burst 3.5s ease-in-out infinite;
}
@keyframes burst {
  0%,100% {
    opacity: .9;
  }
  50% {
    opacity: .6;
  }
}
.draw-path {
  stroke: rgba(255,255,255,.45);
  stroke-width: 2;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  animation: draw 6s linear infinite;
}
@keyframes draw {
  to {
    stroke-dashoffset: 0;
  }
}
.pulse-tile {
  opacity: 0;
  animation: tilePulse 10.8s linear infinite;
  animation-delay: var(--d, 0s);
}
@keyframes tilePulse {
  0% {
    opacity: 0;
  }
  5%, 20% {
    opacity: 1;
  }
  25%, 100% {
    opacity: 0;
  }
}
.panel {
  position: relative;
  border: 1px solid rgba(255,255,255,.12);
  background: rgba(0,0,0,.35);
  border-radius: .5rem;
}
.panel::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,.06), transparent);
  animation: sweep 6s linear infinite;
  opacity: .25;
  pointer-events: none;
}
@keyframes sweep {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.flicker {
  animation: flicker 2.5s infinite;
}
@keyframes flicker {
  0%,100% {
    opacity: .9;
  }
  5% {
    opacity: .6;
  }
  10% {
    opacity: .95;
  }
  15% {
    opacity: .5;
  }
  20% {
    opacity: .9;
  }
}
.cells {
  display: grid;
  grid-template-columns: repeat(20, minmax(0, 1fr));
  gap: 4px;
}
.cell {
  width: 100%;
  padding-top: 60%;
  background: rgba(255,255,255,.08);
  border-radius: 2px;
  animation: pulse 2s ease-in-out infinite;
}
@keyframes pulse {
  0%,100% {
    opacity: .35;
  }
  50% {
    opacity: .1;
  }
}
.bar-stack {
  background: repeating-linear-gradient(
    0deg,
    rgba(255,255,255,.12) 0 10px,
    rgba(255,255,255,.04) 10px 20px
  );
  height: 100%;
  border-radius: .25rem;
}
.eth-diamond {
  position: relative;
  width: 120px;
  height: 120px;
  margin: 0 auto;
}
.eth-diamond::before,
.eth-diamond::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
  width: 80px;
  height: 80px;
  background: linear-gradient(to bottom, rgba(255,255,255,.9), rgba(255,255,255,.2));
  box-shadow: 0 0 40px rgba(255,255,255,.12);
}
.eth-diamond::after {
  top: 50px;
  width: 70px;
  height: 70px;
  background: linear-gradient(to top, rgba(255,255,255,.85), rgba(255,255,255,.15));
}
.glow-bar-list {
  display: grid;
  gap: 10px;
}
.glow-bar {
  height: 24px;
  border-radius: 10px;
  background: radial-gradient(120px 40px at 10% 50%, rgba(255,255,255,.9), rgba(255,255,255,.6) 40%, rgba(255,255,255,.15) 70%, rgba(255,255,255,.05) 100%);
  box-shadow: 0 0 30px rgba(255,255,255,.2) inset, 0 0 8px rgba(255,255,255,.2);
  animation: shimmerBar 3.5s ease-in-out infinite;
}
@keyframes shimmerBar {
  0%,100% {
    filter: brightness(1);
  }
  50% {
    filter: brightness(1.3);
  }
}
.order-list {
  display: grid;
  gap: 6px;
}
.order-row {
  height: 14px;
  border-radius: 2px;
  background: rgba(255,255,255,.08);
}
.risk-tiles {
  display: grid;
  grid-template-columns: repeat(4, minmax(0,1fr));
  gap: 8px;
}
.risk-tile {
  height: 42px;
  border-radius: 4px;
  background: rgba(255,255,255,.08);
}
.stage-rail {
  position: relative;
  height: 2px;
  background: rgba(255,255,255,.12);
  overflow: hidden;
  border-radius: 2px;
}
.stage-rail::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -10%;
  width: 20%;
  height: 6px;
  background: linear-gradient(90deg, rgba(255,255,255,.0), rgba(255,255,255,.7), rgba(255,255,255,.0));
  border-radius: 6px;
  animation: rail-run 3.5s linear infinite;
}
@keyframes rail-run {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(500%);
  }
}
.tx-pulse {
  position: absolute;
  top: 0;
  left: -5%;
  width: 8px;
  height: 8px;
  border-radius: 9999px;
  background: rgba(255,255,255,.8);
  box-shadow: 0 0 12px rgba(255,255,255,.6);
  animation: txflow 2.2s linear infinite;
}
@keyframes txflow {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(115%);
  }
}
.diamond-pulse {
  animation: diamondPulse 4s ease-in-out infinite;
}
@keyframes diamondPulse {
  0%,100% {
    filter: drop-shadow(0 0 10px rgba(255,255,255,.1));
  }
  50% {
    filter: drop-shadow(0 0 24px rgba(255,255,255,.25));
  }
}
